@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Chivo:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  font-family: "chivo", sans-serif;
  overflow-x: hidden;
  position: relative;
  scrollbar-color: black rgb(249, 250, 251);
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background: black;
}

body::-webkit-scrollbar-track {
  background-color: rgb(249, 250, 251);
}

::-moz-selection { /* Code for Firefox */
  color: white;
  background: black;
}

::selection {
  color: white;
  background: black;
}

.tiktok-text {
  text-shadow: 6px -6px 0 #24ece7, -6px 6px 0 #f7004d;
}